/* eslint-disable */
import React, { useState } from "react";
import {
  Container,
  NavbarBrand,
  Navbar,
  Nav,
  NavItem,
  NavLink,
  NavbarToggler,
  Collapse,
} from "reactstrap";

import { useRef } from "react";

import { Link } from "react-router-dom";

// import logo from "%PUBLIC_URL%/Favicon/Logo-01.png";
import logo2 from "../../assets/images/Logo Files/machina.png";

const HeaderComponent = () => {
  const [isOpen, setIsOpen] = useState(false);

  // const handleClick = () => {
  //   about.current?.scrollIntoView({behavior: 'smooth'});
  //   isOpen ? toggle : null;
  // };

  const toggle = () => setIsOpen(!isOpen);

  function reply_click_1() {
    isOpen ? toggle : null;
    let scroll_to = document.getElementById("aboutus").offsetTop;
    window.scrollTo({ behavior: "smooth", top: scroll_to });
  }

  function reply_click_2() {
    isOpen ? toggle : null;
    let scroll_to = document.getElementById("service").offsetTop;
    window.scrollTo({ behavior: "smooth", top: scroll_to });
  }

  return (
    <div>
      <div className="topbar fixed-header   header1 po-relative">
        <Container>
          <Navbar className="navbar-expand-lg h1-nav">
            <NavbarBrand href="#">
              <img src={logo2} width={null} height={50} alt="IzlyTech" />
            </NavbarBrand>
            <NavbarToggler onClick={toggle}>
              <span className="ti-menu"></span>
            </NavbarToggler>
            <Collapse isOpen={isOpen} navbar id="header1">
              <Nav navbar className="ml-auto mt-2 mt-lg-0">
                <NavItem>
                  <NavLink onClick={reply_click_1} href="#">
                    Ã€ PROPOS
                  </NavLink>
                </NavItem>

                <NavItem>
                  <NavLink onClick={reply_click_2} href="#">
                    PROGRAMME
                  </NavLink>
                </NavItem>

                {/* <NavItem>
                  <NavLink href="/#team">Team</NavLink>
                </NavItem> */}

                <NavItem>
                  <a
                    onClick={isOpen ? toggle : null}
                    className="btn"
                    target="_blank"
                    id="contact-btn"
                    href="https://docs.google.com/forms/d/e/1FAIpQLSf9vYlFiu4kfbB3T7SlyJDKe1LHOzPYNJDmlrJL0Fpz_0C0og/viewform"
                  >
                    INSCRIVEZ-VOUS{" "}
                  </a>
                </NavItem>
              </Nav>
            </Collapse>
          </Navbar>
        </Container>
        {/* <hr id="nav_line_1" /> */}
        <hr id="nav_line_2" />
      </div>
    </div>
  );
};

export default HeaderComponent;
